:root{--color-mehroon: #ba4d2c;--color-grey: #617d99;--color-grey1: #324d67}*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;box-sizing:border-box}body{font-size:1.4rem}.container{width:65vw;display:flex;flex-direction:column;margin:7rem auto;gap:5rem}.heading{display:flex;justify-content:center;gap:1rem;font-size:4rem;font-weight:900;font-family:"Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif}.heading--sub{color:var(--color-mehroon)}.heading--main{color:var(--color-grey1)}.slide-list{display:flex;position:relative;width:100%;height:45rem}.slide-list .btn-left,.slide-list .btn-right{width:3rem;height:3rem;color:#fff;background-color:var(--color-grey);display:flex;align-items:center;justify-content:center;position:absolute;cursor:pointer;z-index:10}.slide-list .btn-left svg,.slide-list .btn-right svg{font-size:2rem}.slide-list .btn-left{top:50%;left:0;cursor:pointer}.slide-list .btn-right{top:50%;left:95.5%}.slide{padding:2rem;position:absolute;top:0;left:0;display:flex;flex-direction:column;gap:2.5rem;align-items:center;width:100%;height:100%;transition:all 1s;transform:translateX(100%);opacity:0}.slide__img img{border-radius:50%;width:14rem;height:14rem;object-fit:cover;outline:3px solid #bcccdc}.slide__name{display:flex;flex-direction:column;align-items:center;gap:1rem}.slide__name--name{text-transform:uppercase;color:var(--color-mehroon);font-size:1.5rem;letter-spacing:1.5px;font-weight:900}.slide__name--title{text-transform:capitalize;font-size:1.7rem;color:var(--color-grey1)}.slide__text{color:var(--color-grey1);line-height:2;font-size:1.9rem;word-spacing:1.2px;text-align:center}.slide__icon{color:var(--color-mehroon);font-size:5rem}.activeSlide{transform:translate(0);opacity:1}.nextSlide{transform:translateX(101%)}.prevSlide{transform:translateX(-101%)}
